/* Container for the entire skills section */
.skillsContainer {
    text-align: center;
    padding: 40px 20px;
  }
  
  /* Grid layout for the skill cards */
  .skillsGrid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    margin-top: 20px;
  }
  
  /* Individual skill card styling */
  .skillCard {
    background: #ffffff;
    border-radius: 10px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    padding: 20px;
    text-align: left;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    border-bottom: 4px solid transparent;
  }
  
  .skillCard:hover {
    transform: translateY(-5px);
    box-shadow: 0px 8px 10px rgba(0, 0, 0, 0.15);
  }
  
  /* Icon styling */
  .iconContainer {
    font-size: 36px; /* Size of the icon */
    margin-bottom: 10px;
    color: #333; /* Default color for icons */
  }
  
  /* Title styling for each skill */
  .skillTitle {
    font-weight: bold;
    font-size: 18px;
    margin-bottom: 10px;
    color: #333;
  }
  
  /* Chip container styling */
  .skillItems {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    margin-top: 0px;
  }
  
  /* Individual chip styling */
  .skillChip {
    background-color: #ffffff;
    color: #333;
    padding: 3px 5px;
    font-size: 12px;
    border-radius: 8px;
    border: 1px solid #ddd;
    transition: background-color 0.3s ease;
  }
  
  .skillChip:hover {
    background-color: #e0e0e0;
    cursor: pointer;
  }
  