/* Sidebar styles */
.sidebar {
    position: fixed;
    top: 0;
    left: 0;
    width: 250px; /* Adjust sidebar width */
    height: 100vh; /* Full viewport height */
    background-color: #f4f4f4; /* Light gray background */
    padding: 20px; /* Inner padding */
    text-align: center;
    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1); /* Subtle shadow */
  }
  
  .profileImage {
    width: 150px;
    height: 150px;
    border-radius: 50%; /* Circular image */
    margin-bottom: 20px;
    object-fit: cover;
  }
  
  h1 {
    margin-bottom: 10px;
    font-size: 24px;
    color: #333;
  }
  
  .jobTitle {
    font-size: 16px;
    color: #666;
    margin-bottom: 20px;
  }
  
  /* Contact Icons Section */
  .contactIcons {
    display: flex;
    justify-content: center; /* Align icons in the center */
    gap: 15px; /* Space between icons */
    margin-bottom: 30px; /* Space below the icons */
  }
  
  .iconLink {
    font-size: 24px; /* Adjust icon size */
    color: #333;
    transition: color 0.3s ease;
  }
  
  .iconLink:hover {
    color: #007bff;
  }
  
  /* Navigation Links */
  .navLinks {
    margin: 20px 0;
    padding: 0;
  }
  
  .navLinks li {
    cursor: pointer;
    padding: 10px;
    font-size: 18px;
    color: #007bff;
    transition: color 0.3s ease;
    list-style: none; /* Remove bullets */
  }
  
  .navLinks li:hover {
    color: #0056b3;
  }
  
  /* Footer */
  .footer {
    margin-top: 20px;
    font-size: 14px;
    color: #666;
    text-align: center;
  }
  
  .footer p {
    margin: 0;
  }
  