.mainContent {
    margin-left: 300px; /* Move the content further right by increasing the margin */
    padding: 20px;      /* Keep some padding inside the content area */
  }
  
  section {
    margin-bottom: 80px; /* Space between sections */
  }
  
  section h2 {
    margin-bottom: 20px;
  }
  
  ul {
    margin: 10px 0 20px 20px; /* Add spacing around the list */
    padding: 0; /* Remove default padding */
    list-style-type: disc; /* Use dots as bullet points */
  }
  
  li {
    margin-bottom: 10px; /* Add spacing between bullet points */
    font-size: 16px; /* Adjust font size for better readability */
    color: #333; /* Set text color to a readable dark gray */
  }
  